.radio-box-field {
  @include fonts($fontOswaldRegular, 400, normal, 16px, 18px);
  text-transform: uppercase;
  border: 0;
  padding: 0;
  margin: 0;
  position: relative;
  width: 100%;

  @media (max-width: $mobile-only) {
    border-top: 1px solid $line-color;
    border-bottom: 1px solid $line-color;
    padding: 21px 20px 6px 20px;
  }

  @media (min-width: $tablet-width) {
    @include fontSizeColor(20px, 30px);
    border: 1px solid $line-color;
    padding: 20px 25px 17px 28px;
    margin: 0 0 75px 0;
    min-height: 197px;
  }

  @media (min-width: $desktop-width) {
    max-width: 1320px;
    padding: 22px 50px 18px 29px;
    margin: 0 auto;
  }
}
